import React from 'react';

export default ({ updateState, getVideoElement, getPlayerElement }) => {
  const requestFullscreen = React.useCallback(() => {
    const el = getPlayerElement();
    if (!el) {
      // debug('useVideoFullscreen: unmounted');
      return;
    }
    if (el.requestFullscreen) {
      el.requestFullscreen();
    } else if (el.msRequestFullscreen) {
      //  IE11
      el.msRequestFullscreen();
    } else if (el.webkitRequestFullscreen) {
      //  PC 
      el.webkitRequestFullscreen();
    } else {
      const videoEl = getVideoElement();
      if (videoEl && videoEl.webkitEnterFullScreen) {
        //  iOS Safari
        videoEl.webkitEnterFullScreen();
      } else {
        // 
        // debug('useVideoFullscreen:  requestFullscreen');
      }
    }
  }, [getVideoElement, getPlayerElement]);

  const exitFullscreen = React.useCallback(() => {
    if (document.exitFullscreen) {
      document.exitFullscreen();
    } else if (document.msExitFullscreen) {
      document.msExitFullscreen();
    } else if (document.webkitExitFullscreen) {
      document.webkitExitFullscreen();
    } else {
      // 
      // debug('useVideoFullscreen:  exitFullscreen');
    }
  }, []);

  const onChange = React.useCallback(() => {
    const el = getPlayerElement();
    const fullscreenElement =
      document.fullscreenElement || document.webkitFullscreenElement || document.msFullscreenElement;
    updateState({ fullscreen: !!el && fullscreenElement === el });
  }, [updateState, getPlayerElement]);

  React.useEffect(() => {
    document.addEventListener('fullscreenchange', onChange);
    document.addEventListener('webkitfullscreenchange', onChange);
    document.onmsfullscreenchange = onChange;
    return () => {
      document.removeEventListener('fullscreenchange', onChange);
      document.removeEventListener('webkitfullscreenchange', onChange);
      document.onmsfullscreenchange = null;
    };
  }, [onChange]);

  return { requestFullscreen, exitFullscreen };
};
